import axios, { AxiosRequestConfig, AxiosResponse } from 'axios';
import { message } from 'ant-design-vue';
import { MessageType } from 'ant-design-vue/lib/message';
import { CustomConfig, responseType } from '../../types/common';
import cancel from '../../api/axios/cancel';
import configs from '@/configs';

let hideGlobalMessage: MessageType;

const request = (AxiosConfig: AxiosRequestConfig, customConfig: CustomConfig) => {
	// 
	const instance = axios.create({
		baseURL: configs.baseUrl,
		timeout: 10000,
	});

	instance.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';

	const customConfigs = {
		repeatRequestCancel: true, // true
		loading: true, // loading, true
		dataFormat: true, // , true
		useMsgFromEnd: true, // 
		loadingTitle: '', // loading
		successTitle: '', // success
		errorTitle: '', // error
		...customConfig, // 
	};

	// 
	instance.interceptors.request.use(
		(config): AxiosRequestConfig<any> => {
			customConfigs.repeatRequestCancel && cancel.removePendingRequest(config); // 

			customConfigs.loading && (hideGlobalMessage = message.loading(customConfigs.loadingTitle, 0));

			customConfigs.repeatRequestCancel && cancel.addPendingRequest(config); // pendingRequest
			const token = window.sessionStorage.getItem('token');
			if (token) {
				// @ts-ignore
				config.headers.token = token;
			}
			return config;
		},
		error => {
			return Promise.reject(error);
		}
	);

	// 
	instance.interceptors.response.use(
		(res: AxiosResponse<responseType, any>) => {
			customConfigs.repeatRequestCancel && cancel.removePendingRequest(res.config); // pendingRequest
			customConfigs.loading && hideGlobalMessage();
			if (res.data.code === 401) {
				sessionStorage.setItem('token', '');
				// token
			} else if (res.data.code === 1) {
				// loading
				customConfigs.loading &&
					message.error({
						content: customConfigs.useMsgFromEnd && res.data.message ? res.data.message : customConfigs.successTitle,
						duration: 2,
					});
			} else if (typeof res.data === 'string') {
				customConfigs.loading && hideGlobalMessage();
				return customConfigs.dataFormat ? res.data : res;
			} else if (res.data.code === 0) {
				// loading
				customConfigs.loading &&
					message.success({
						content: customConfigs.useMsgFromEnd && res.data.message ? res.data.message : customConfigs.successTitle,
						duration: 2,
					});
				return customConfigs.dataFormat ? res.data : res;
			}
			return Promise.reject(res);
		},
		err => {
			customConfigs.repeatRequestCancel && cancel.removePendingRequest(err.config || {}); // pendingRequest
			if (axios.isCancel(err)) {
				console.warn(`:${err.message}`);
			} else {
				// loading
				customConfigs.loading && hideGlobalMessage();
				customConfigs.loading &&
					message.error({
						content: customConfigs.useMsgFromEnd && err.data.msg ? err.data.msg : customConfigs.errorTitle,
						duration: 2,
					});
			}
			return Promise.reject(err);
		}
	);
	return instance(AxiosConfig);
};

const createRequest = (config: AxiosRequestConfig, customConfig?: CustomConfig) => {
	return new Promise((resolve, reject) => {
		request(config, customConfig || {})
			.then(res => {
				resolve(res);
			})
			.catch(err => {
				reject(err);
			});
	});
};

export default createRequest;
